html,body,#root{
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: 'Yeon Sung', sans-serif;
    /* background-color:#cf635b !important; */
    background-attachment: fixed !important;
    background-image:linear-gradient(#090979, #00d4ff)
  }
  .height100{
    height: 100%;
    margin-left:50px;
    margin-bottom:300px;
  
  }
  
  
  
  .header h3{
    margin:auto;
    color:#fff;
  }
  
  
  .list ul{
   text-decoration: none;
   padding-left: 0;
  
  list-style: none;
  
  text-align: left;
  
  -webkit-padding-start: 0;
  
  position: absolute;
  
  width: 100%;
  }
  
  .list ul li{
    color:#000;
    background-color:#fff; 
    padding: 5px 10px;
    border-bottom:1px solid #555;
    cursor: pointer;
  }
  
  
  
  .results__items {
    margin-top:50px;
    display: flex;
    box-shadow: 0 3px 20px rgba(0, 0, 0, 0.2);
    background:transparent;
    width:fit-content;
    list-style: none;
    flex-direction: column;
    text-transform: uppercase;
    margin-right: 20px;
    vertical-align: middle;
    margin-left: 20px;
    float:left;
    height:fit-content;
    
    }
    
  
  
  .results__title {
  display: block;
  text-align:center;   
  margin: 0 0 10px;
  border-bottom: 1px solid #d8d499;
  padding-bottom: 3px;
  font-family: "Josefin Slab", serif;
  font-size: 1.5rem;
  color: #fff;
  }
  
  
  .results__info {
  text-align: justify;
  margin: 0;
  padding: 20px;
  line-height: 2rem;
  list-style: none;
  text-align: left;
  color:yellow;
  float:left;
  }
  .results__info span {
    margin:0;
    padding:0;
    float:right;
  color:red;
  }
  
  .logincontainer{
    margin-top:50px;
    width:400px !important;
    
  }
  img{
    border-radius: 100%;
    height:"100px";
    width:"100x";
    margin-bottom:50px;
  
  }
  
  
  
  
  .searchBar{
    margin-top:20px !important;
    display:inline-block;
    width:80%;
    margin-top:0;
    
  }
  .logout{
    display:inline-flex;
    width:15%;
    margin-top:10px;
    margin-left:10px;
    margin-right:10px;
    flex-direction:column;
    float:right;
   
  }
  .logout Button{
    width:fit-content;
    align-self: center;
  }
  
  div{
    margin:0px;
    padding:0px;
  }
  